@import "../base/mixins.scss";
@import "../base/vars.scss";

.contact {
  padding: 100px 0 110px;

  background-color: $color_blue_sky;
  background-image: url("../img/plane-desktop@1x.png"), url("../img/bg-contact-delta@1x.png");
  background-repeat: no-repeat;
  background-position: left 13% center, center;
  background-size: 33%, cover;

  @media (min-resolution: $retina_dpi) and (min-resolution: $retina_dppx) {
    background-color: $color_blue_sky;
    background-image: url("../img/plane-desktop@2x.png"), url("../img/bg-contact-delta@2x.png");
    background-repeat: no-repeat;
    background-position: left 13% center, center;
    background-size: 33%, cover;
  }

  @media (max-width: $width_viewport_tablet) {
    padding: 90px 0 103px;

    background-image: none;
  }

  @media (max-width: $width_viewport_mobile) {
    padding: 40px 0 45px;
  }
}

.contact__inner {
  @include inner_thin;

  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;

  @media (max-width: $width_viewport_mobile) {
    display: block;
  }
}
